@import url(https://fonts.googleapis.com/css?family=Roboto+Mono|Roboto:300,400,500);
body{height:auto !important;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}header{position:sticky;top:0;padding-left:20px;height:62px;width:100%;background-color:#b0b6b8;border-bottom:1px solid rgba(34,36,38,0.15);box-shadow:0 2px 6px 0 rgba(34,36,38,0.15),0 2px 18px 0 rgba(34,36,38,0.17);z-index:102}header .ui.header{display:flex !important;flex-direction:row;align-items:stretch;vertical-align:middle}header .content{padding-top:16px;padding-left:1.5em !important}header .ui.header img.ui.image{margin-top:3px;width:2.2em;margin-right:15px;margin-left:20px}nav{position:fixed;top:60px;width:125px;min-height:100%;transition:-webkit-transform 0.5s ease;transition:transform 0.5s ease;transition:transform 0.5s ease, -webkit-transform 0.5s ease;z-index:101}nav .ui.menu.app-nav.red{min-height:100vh !important;border:none !important;border-radius:0 !important}nav .ui.menu.app-nav.red .item:last-child{border-radius:0 !important;border-bottom:solid 1px rgba(0,0,0,0.15) !important}nav .ui.menu.app-nav.red .active.item{border-color:#c03 !important;color:#c03 !important}.container{max-width:calc(100% - 125px);margin-left:180px;margin-top:40px;margin-right:40px}

.categories-container .ui.grid .column .ui.card{cursor:pointer}.categories-container .ui.grid .column .ui.card:hover{box-shadow:0 0 21px rgba(33,33,33,0.2);-webkit-transform:translateY(1px);transform:translateY(1px)}

.container .latest-meals-container .ui.items>.item{padding-bottom:10px;border-bottom:1px solid}.container .latest-meals-container .ui.items>.item .content .label{margin-left:10px}

.container .random-meal-container .ui.items>.item{padding-bottom:10px}.container .random-meal-container .ui.items>.item .content .label{margin-left:10px}

.category-container .back-link{float:right;font-size:14px;cursor:pointer;color:blue}.category-container .back-link span{text-decoration:underline}.category-container .ui.grid .column .ui.card{cursor:pointer}.category-container .ui.grid .column .ui.card:hover{box-shadow:0 0 21px rgba(33,33,33,0.2);-webkit-transform:translateY(1px);transform:translateY(1px)}

.ui.modal.meal-details-dialog .content .description{margin-bottom:20px}.ui.modal.meal-details-dialog .content .description .ui.list .column .header{font-weight:bolder}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.not-found-page{text-align:center;padding-top:50px}.not-found-page .error{font-size:160px;margin-bottom:90px}.not-found-page .page-msg{font-size:40px;margin-bottom:40px}

